import {Component,OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {ConstantService} from '../../core/services/constant.service';
import {LocalStorage} from '../../core/services/localstorage.service';
import {SystemSetService} from './system-set.service';
@Component({
  selector: 'system-set',
  templateUrl: './system-set.component.html',
  styleUrls: ['./system-set.component.less']
})
export class SystemSetComponent implements OnInit{
  private protocolList = ConstantService.protocolList;
  private systemEmail:any = {protocol:'SMTP',changePassword:0,TLS:0};
  constructor(private service:SystemSetService){}
  ngOnInit(){
    this.service.getSystemEmail();
  }
  selectItem(item,checked:any){
    if(checked){
      item = 1;
    }else{
      item = 0;
    }
  }
  saveEmail(){
    let query = Object.assign({},this.systemEmail);
    for(var key in query){
      if(query[key] === true){
        query[key] = 1;
      }else if(query[key] === false){
        query[key] = 0;
      }
    }
    console.log(query);
    this.service.editSystemEmail(query);

  }
}



