import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {RoleManageService} from '../role-manage.service';
@Component({
  selector:'user-form',
  templateUrl:'./role.dialog.html',
  styleUrls:['./role.dialog.less']
})

export class RoleDialog implements OnInit{
  static roleConfig: MdDialogConfig = {
    width: '600px',
    data: {id:''}
  };
  ngOnInit(){
    this.data.status = 1;
  }
  constructor(
    public dialogRef:MdDialogRef<RoleDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:RoleManageService
  ){

  }
  saveRole(data:any){
    let params = Object.assign({},data);
    delete params.id;
    if(data.id!=''){
      this.service.editRole(data.id,params).subscribe(res=>{
        if(res.data.status == 200){
          this.data.getRoleList();
        }
      });
    }else{
      delete params.id;
      this.service.createRole(params).subscribe(res=>{
        if(res.data.status == 200){
          this.data.getRoleList(0);
        }
      });
    }
    this.dialogRef.close();
  }
}



