import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {LocalStorage} from '../../core/services/localstorage.service';
import {Observable} from "rxjs";
@Injectable()
export class LogManageService {
  @observable loginLogList:any = [];
  @observable operaLogList:any = [];

  constructor(private connectionService:ConnectionService){}
  retriveLoginLog(params){
    let path = '/api/log_login';
    const logObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    logObservable.subscribe(res=>{
      this.loginLogList.length = 0;
      this.loginLogList.push.apply(this.loginLogList,res.data.result.pageData);
    });
    return logObservable;
  }

  retriveOperationLog(params){
    let path = '/api/log_action';
    const logObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    logObservable.subscribe(res=>{
      this.operaLogList.length = 0;
      this.operaLogList.push.apply(this.operaLogList,res.data.result.pageData);
    });
    return logObservable;
  }
}



