/*
 其它任何模块中都不要导入BrowserModule。 特性模块和惰性加载模块应该改成导入CommonModule。
 它们需要通用的指令。它们不需要重新初始化全应用级的提供商。
 * */

import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { FormsModule }    from '@angular/forms';
import {RouterModule} from "@angular/router";

import {AppHomeComponent} from "./app-home.component";
import {AuthManageModule} from './auth-manage/auth-manage.module';
import {RoleManageModule} from "./role-manage/role-manage.module";
import {SystemSetModule} from "./system-set/system-set.module";
import {UserManageModule} from "./user-manage/user-manage.module";
import {PasswordManageModule} from "./password-manage/password-manage.module";
import {AppManageModule} from "./app-manage/app-manage.module";
import {ClientAuthRoutingModule} from "./password-manage/password-routing.module"
//import {AuthManageComponent} from './auth-manage/auth-manage.component';
// 在exports中，把需要在home模块外的组件export出去，就可以在app中的其他模块中使用该组件
// LeftBookComponent，这样其它模块只要导入了HomeModule，就可以在它们的组件模板中使用LeftBookComponent。
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule,
    PasswordManageModule,
    AuthManageModule,
    RoleManageModule,
    SystemSetModule,
    UserManageModule,
    AppManageModule,
    ClientAuthRoutingModule
  ],
  declarations: [
    AppHomeComponent,
    //AuthManageComponent
  ],
  entryComponents: [

  ],
  exports:[
  ],
  providers: []
})
export class LoginManageModule {}



