import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {UserManageService} from '../user-manage.service';
@Component({
  selector:'user-role',
  templateUrl:'./user-role.dialog.html',
  styleUrls:['./user-role.dialog.less']
})

export class UserRoleDialog implements OnInit{
  public static roleConfig: MdDialogConfig = {
    width: '600px',
    data: {}
  };
  private roleList:any;
  private userRoleList:any = [];
  private usedRoleList:any = [];
  private roleIdList:any = [];
  constructor(
    public dialogRef:MdDialogRef<UserRoleDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:UserManageService
  ){
      this.roleList = this.service.roleList;
      this.userRoleList = this.service.userRoleList;
  }

  ngOnInit(){
    this.roleList.map(item=>{item.choosed = false;});
    let array = this.roleList.filter(item=>this.userRoleList.findIndex(role=>role.id === item.id) == -1);
    this.usedRoleList = array.slice(0);
    this.userRoleList.map((item:any)=>{item.choosed = true;})
  }
  saveUser(data:any){
    this.dialogRef.close();
  }

  chooseItem(item:any){
    item.choosed = !item.choosed;
  }

  joinGroup(){
    let list = this.usedRoleList.filter(item=>item.choosed==true);
    this.userRoleList.push.apply(this.userRoleList,list);
    this.usedRoleList = this.usedRoleList.filter(item=>item.choosed!=true);
    this.roleIdList.length = 0;
    this.userRoleList.forEach(item=>{
      this.roleIdList.push(item.id);
    });

    let params = this.roleIdList;
    this.service.editUserRole(this.data.id,params);
  }

  removeGroup(){
    let removeList = this.userRoleList.filter(item=>item.choosed == false);
    this.usedRoleList.push.apply(this.usedRoleList,removeList);
    this.userRoleList = this.userRoleList.filter(item=>item.choosed != false);
    this.roleIdList.length = 0;
    this.userRoleList.forEach(item=>{
      this.roleIdList.push(item.id);
    });
    let params = this.roleIdList;
    this.service.editUserRole(this.data.id,params);
  }

  back(){
    this.data.getUserList();
    this.dialogRef.close('cancel')
  }
}



