import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import {
  MdButtonModule,
  MdCheckboxModule,
  MdCardModule,
  MdInputModule,
  MdToolbarModule,
  MdTooltipModule,
  MdTabsModule,
  MdIconModule,
  MdMenuModule,
  MdDialogModule,
  MdListModule,
  MdSelectModule,
  MdSidenavModule,
  MdSliderModule,
  MdSlideToggleModule,
  MdRadioModule,
  MdDatepickerModule,
  MdNativeDateModule,
  MdProgressSpinnerModule
} from '@angular/material';
import {MD_DATE_FORMATS} from "@angular/material/typings/core/datetime/date-formats";

@NgModule({
  exports: [
    CommonModule,
    MdButtonModule,
    MdCheckboxModule,
    MdCardModule,
    MdInputModule,
    MdToolbarModule,
    MdTooltipModule,
    MdTabsModule,
    MdIconModule,
    MdMenuModule,
    MdDialogModule,
    MdListModule,
    MdProgressSpinnerModule,
    MdSelectModule,
    MdSidenavModule,
    MdSliderModule,
    MdRadioModule,
    MdDatepickerModule,
    MdNativeDateModule,
    MdSlideToggleModule
  ],
  declarations: [],
})
export class CustomizeMaterialModule { }



