import {Component,OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {SwalService} from '../services/swal.service';
import {LocalStorage} from "../services/localstorage.service";
import {ConnectionService} from "../services/connection.service";

@Component({
  selector: 'header',
  templateUrl: 'header.component.html',
  styleUrls: ['header.component.less'],
})

export class HeaderComponent implements OnInit{
  private user = LocalStorage.get('user');

  constructor(
    private router: Router,
    private connectionService:ConnectionService,
    private swalService:SwalService
  ) {

  }

  ngOnInit(){
    if(!LocalStorage.get('user')){
      this.getCurrentUser();
    }
  }


  exit(){
    LocalStorage.remove('user');
    let path = '/logout';
    this.connectionService.get(path).then(res => {
    }).catch(error =>{
      //console.log(error)
    });
    window.location.reload();
  }

  /*
   *@desc 获取当前用户信息
   */
  getCurrentUser(){
    let userPath = '/api/user/user_info';
    this.connectionService.get(userPath)
      .then((res:any) => {
        if(res.data.status == 200){
          LocalStorage.set('user',res.data.result);
          this.user = LocalStorage.get('user')
        }else{
          //window.location.reload();
        }
      })
      .catch(err => {
        //window.location.reload();
      });
  }
}





