import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {RoleManageService} from '../role-manage.service';
@Component({
  selector:'look-form',
  template:`
    <div class="main-form">
      <div class="content" style="margin-top:10px;">
         <ul>
           <li *ngFor="let group of groupValue" class="auth-block">
             <h1>{{group.applicationName}}({{group.applicationKey}})</h1>
             <div>
               <ul>
                 <li *ngFor="let item of authList">
                   <div  *ngIf="item.applicationKey === group.applicationKey" class="auth">
                     <div class="checkbox-item">
                       <md-checkbox name="cb" value="basic_checkbox"
                         [color]="'primary'"
                         [(ngModel)]="item.selected"
                         (change)="selectOneItem(item,$event.checked)">
                       </md-checkbox>
                     </div>
                     <span>{{item.name}}({{item.key}})</span>
                   </div>
                 </li>
               </ul>
             </div>
           </li>
         </ul>
      </div>
      <div class="opt-button">
        <button md-button  style="margin-right:5px;" (click)="saveAuth()">保存</button>
        <button md-button class="cancel-button" style="margin-right:5px;" (click)="dialogRef.close('cancel')">取消</button>
      </div>
    </div>
  `,
  styles:[`
    .main-form{
      margin-top:-24px;
    }
    .content ul{
      list-style:none;
      padding:0;
      margin:0;
    }
    h1{
      clear:both;
      font-size:20px;
      font-weight:700;
      padding-bottom:20px;
      padding-top:24px;
    }
    .auth-block{
      margin-bottom:20px;
    }
    .auth{
      float:left;
      width:50%;
      margin-bottom:10px;
      font-size:13px;
      color:#333;
    }
    .checkbox-item{
      display:inline-block;
    }
    .opt-button{
      clear:both;
      padding-top:24px;
    }
    .opt-button button{
      background:#1194F6;
      color:#fff;
      font-size:13px;
      line-height:28px;
      min-width:57px !important;
    }
    .cancel-button{
      background:none !important;
      color:#333 !important;
    }
  `]
})

export class RoleManageDialog{
  static roleConfig: MdDialogConfig = {
    width: '600px',
    data: {}
  };

  private roleAuthList:any = [];
  private authList:any = [];
  private groupValue:any = [];
  private permissionIdList:any = [];
  constructor(
    public dialogRef:MdDialogRef<RoleManageDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:RoleManageService
  ){
    this.roleAuthList = this.service.roleAuthList;
    this.authList = this.service.authList;
    this.groupValue = this.service.groupValue;
    this.permissionIdList = this.service.permissionIdList;
  }


  /**
   * @desc 点击某权限对应的复选框
   * @param item - 被点击的权限详细
   * @param checked - 权限是否被选中
   */
  selectOneItem(item,checked){
    item.selected = checked;
    const index = this.permissionIdList.indexOf(item.id);
    if(checked){
      if(index === -1){
        this.permissionIdList.push(item.id);
      }
    }else{
      if(index > -1){
        this.permissionIdList.splice(index,1);
      }
    }
  }

  /**
   * @desc 保存角色权限
   */
  saveAuth(){
    //let params = {
    //  permissionIdList:this.permissionIdList
    //};
    let params = this.permissionIdList
    this.service.editRoleAuth(this.data.id,params);
    this.dialogRef.close();
  }
}



