import {Component,Input,Output,OnInit,EventEmitter,enableProdMode} from '@angular/core';
import {MdSelectModule} from '@angular/material';
import {RoleManageService} from './role-manage.service';
import {MdDialog} from '@angular/material';
import {AddMemberDialog} from './dialogs/add-member.dialog';
import {RoleDialog} from './dialogs/role.dialog';
import {RoleManageDialog} from './dialogs/role-manage.dialog';
import {Router} from '@angular/router';
import swal from 'sweetalert2';
enableProdMode();
@Component({
  selector:'role-list',
  templateUrl:'role-list.component.html',
  styles:[``]
})
export class RoleListComponent{
  @Input() offset;
  @Input() getRoleList;
  private roleList;
  constructor(
    private service:RoleManageService,
    private _dialog:MdDialog,
    private router:Router){
    this.roleList = this.service.roleList;
  }


  /**
   * @desc 删除用户
   * @param item - 用户信息
   */
  removeRole(item:any,index){
    swal({
      title: '您确定删除该角色吗?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: '是的, 删除!',
      cancelButtonText: '不, 再想想'
    }).then(
      ()=>{
        this.service.removeRole(item.id,index).subscribe(res=>{
          if(res.data.status == 200){
            this.getRoleList();
          }
        });
      }, function(dismiss) {
        // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
        if (dismiss === 'cancel') {
        }
      });
  }


  /**
   * @desc 编辑角色用户
   * @param item - 角色信息
   */
  editRole(item:any){
    this.router.navigate(['/index/role/user_manage',item.id]);
  }

  editRoleItem(item,index){
    let roleConfig = RoleDialog.roleConfig;
    roleConfig.data = item;
    roleConfig.data.getRoleList = this.getRoleList;
    this._dialog.open(RoleDialog,roleConfig);
  }


  /**
   *@desc 角色的权限管理
   *@param item - 被操作的角色详情
   */
  manageAuth(item:any){
    let params = {
      start:0,
      limit:999999,
      roleId:item.id
    }
    this.service.retriveRoleAuth(params).subscribe(res=>{
      let roleConfig = RoleManageDialog.roleConfig;
      roleConfig.data.id = item.id;
      this._dialog.open(RoleManageDialog,roleConfig);
    });

  }



}



