import {Component,OnInit,OnDestroy} from '@angular/core';
import {Router} from "@angular/router";
import {LocalStorage} from '../../core/services/localstorage.service';
import {MdDialog} from '@angular/material';
import {AuthManageService} from './auth-manage.service';
import {AuthDialog} from './dialogs/auth.dialog';
@Component({
  selector: 'auth-manage',
  templateUrl: './auth-manage.component.html',
  styleUrls: ['./auth-manage.component.less']
})
export class AuthManageComponent implements OnInit,OnDestroy{
  private pageConfig:any = {
    totalItems:0,
    skips:[10,20,50,100],
  };
  private search:any = {
    currentPage:1,
    start:0,
    limit:10,
    applicationId:'all'
  };
  private appList:any = [];
  private authList:any = [];
  constructor(private _dialog:MdDialog,private service:AuthManageService){
    this.appList = this.service.appList;
    this.authList = this.service.authList;
  }

  ngOnInit(){
    if(LocalStorage.get('authParams')){
      this.search = LocalStorage.get('authParams');
      this.getAuthList();
    }else{
      this.getAuthList();
    }
    this.service.reloadApp();
  }

  ngOnDestroy(){
    LocalStorage.remove('authParams');
  }

  getAuthList = (start?:any)=>{
    if(start == 0){
      this.search.currentPage = 1;
    }
    this.search.start = this.search.limit * (this.search.currentPage-1);
    LocalStorage.set('authParams',this.search);

    if(this.search.key){
      this.search.key = this.search.key.replace(/\s/g, "");
    }
    let query = Object.assign({},this.search);
    for(var key in query){
      if(query[key]!=0 && !query[key]){
        delete query[key];
      }
    }
    delete query.currentPage;
    if(query.applicationId == 'all'){
      delete query.applicationId;
    }
    this.service.retriveAuthList(query).subscribe(res=>{
      if(res.data.result){
        this.pageConfig.totalItems = res.data.result.totalSize;
      }
    },error=>{
    })
  }
  changeLimit(skip){
    this.search.currentPage = 1;
    this.search.limit = skip;
    this.getAuthList();
  }

  addAuth(){
    let authConfig = AuthDialog.authConfig;
    authConfig.data = {key:'',name:'',appList:this.appList,getAuthList:this.getAuthList};
    if(this.appList[0] && this.appList[0].id){
      authConfig.data.applicationId = this.appList[0].id;
    }
    this._dialog.open(AuthDialog,authConfig);
  }

  /**
   * @desc 选择关键词分类后查询该分类下的关键字
   * @param group - 关键词分类的id
   */
  searchByKey(event:any){
    let keyCode=event ? event.keyCode : event.which;
    if(keyCode===13){
      this.getAuthList();
    }
  }

}



