import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {UserManageService} from '../user-manage.service';
@Component({
  selector:'user-form',
  templateUrl:'./user-form.dialog.html',
  styleUrls:['./user-form.dialog.less']
})

export class UserFormDialog{
  static formConfig: MdDialogConfig = {
    width: '600px',
    data: {
    }
  };
  constructor(
    public dialogRef:MdDialogRef<UserFormDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:UserManageService
  ){
  }



  saveUser(data:any){
    if(!data.id){
      let params = Object.assign({},data);
      delete params.id;
      delete params.departmentList;
      delete params.verify;
      this.service.createUser(params).subscribe(res=>{
        if(res.data.status == 200){
          this.data.getUserList(0);
        }
      });
    }else{
      let params = {
        username:data.username,
        fullName:data.fullName,
        departmentId:data.departmentId,
        email:data.email,
        status:data.status
      };
      this.service.editUser(data.id,data.index,params).subscribe(res=>{
        if(res.data.status == 200){
          this.data.getUserList();
        }
      });
    }

    this.dialogRef.close();
  }

  verifyResult(){
    return this.data.password && this.data.verify && this.data.password == this.data.verify;
  }

}



