import {Component,OnInit,OnDestroy} from '@angular/core';
import {Router} from "@angular/router";
import {LocalStorage} from '../../core/services/localstorage.service';
import {MdDialog} from '@angular/material';
import {RoleDialog} from './dialogs/role.dialog';
import {RoleManageService} from './role-manage.service';
@Component({
  selector: 'role-manage',
  templateUrl: './role-manage.component.html',
  styleUrls: ['./role-manage.component.less']
})
export class RoleManageComponent implements OnInit,OnDestroy{
  private pageConfig:any = {
    totalItems:0,
    skips:[10,20,50,100],
  };
  private search:any = {
    currentPage:1,
    start:0,
    limit:10,
    roleId:'all'
  };

  constructor(private _dialog:MdDialog,private service:RoleManageService){}

  ngOnInit(){
    if(LocalStorage.get('userParams')){
      this.search = LocalStorage.get('userParams');
      this.getRoleList();
    }else{
      this.getRoleList();
    }
    this.service.reloadAuthList({start:0,limit:999999});
    this.service.reloadAllUsers();
  }


  ngOnDestroy(){
    LocalStorage.remove('roleParams')
  }

  /**
   * @desc 查询角色列表
   */
  getRoleList = (start?:any)=>{
    if(start == 0){
      this.search.currentPage = 1;
    }
    this.search.start = this.search.limit * (this.search.currentPage-1);
    LocalStorage.set('roleParams',this.search);
    let query:any = {};
    for(let i in this.search){
      if(this.search[i] == 0 || this.search[i] && this.search[i] != undefined){
        query[i] = this.search[i];
      }
    }
    if(query.roleId == 'all'){
      delete query.roleId;
    }
    delete query.currentPage;
    this.service.retriveRoleList(query).subscribe(res=>{
      if(res.data.result){
        this.pageConfig.totalItems = res.data.result.totalSize;
      }
    },error=>{
    })
  }

  /**
   * @desc 切换页面大小
   */
  changeLimit(skip){
    this.search.currentPage = 1;
    this.search.limit = skip;
    this.getRoleList();
  }

  /**
   * @desc 新建角色
   */
  addRole(){
    let roleConfig = RoleDialog.roleConfig;
    roleConfig.data.getRoleList = this.getRoleList;
    this._dialog.open(RoleDialog,roleConfig);
  }

  /**
   * @desc 选择关键词分类后查询该分类下的关键字
   * @param group - 关键词分类的id
   */
  searchByKey(event:any){
    let keyCode=event ? event.keyCode : event.which;
    if(keyCode===13){
      this.getRoleList();
    }
  }
}



