import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';

@Component({
  selector:'app-form',
  templateUrl:'./app.dialog.html',
  styleUrls:['./app.dialog.less']
})

export class AppDialog implements OnInit{
  static appConfig: MdDialogConfig = {
    width: '600px',
    data: {}
  };
  ngOnInit(){
    this.data.status = 1;
  }
  constructor(
    public dialogRef:MdDialogRef<AppDialog>,
    @Inject(MD_DIALOG_DATA) public data:any
  ){

  }
  saveApp(data:any){
    //console.log(data);
    this.dialogRef.close(data);
  }
}



