import {Component,Input,Output,OnInit,EventEmitter,enableProdMode} from '@angular/core';
import {MdSelectModule} from '@angular/material';
import {AuthManageService} from './auth-manage.service';
import {MdDialog,MdDialogRef} from '@angular/material';
import {LookRoleUserDialog} from './dialogs/look-role-user.dialog';
import swal from 'sweetalert2';
enableProdMode();
@Component({
  selector:'auth-list',
  templateUrl:'auth-list.component.html',
  styles:[``]
})
export class AuthListComponent{
  @Input() offset;
  @Input() getAuthList;
  private authList;
  private appList;

  constructor(private service:AuthManageService,private _dialog:MdDialog){
    this.authList = this.service.authList;
    this.appList = this.service.appList;
  }


  /**
   * @desc 删除权限
   * @param item - 权限信息
   */
  removeAuth(item:any,index){
    swal({
      title: '您确定删除该权限吗?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: '是的, 删除!',
      cancelButtonText: '不, 再想想'
    }).then(
      ()=>{
        this.service.removeAuth(item.id,index).subscribe(res=>{
          if(res.data.status == 200){
            this.getAuthList();
          }
        });
      }, function(dismiss) {
        // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
        if (dismiss === 'cancel') {
        }
      });
  }


  /**
   * @desc 查看权限的角色与用户
   * @param item - 权限信息
   */
  look(item:any){
    this.service.retirveAuthRoleUser(item.id).subscribe(([role,user])=>{
      let lookConfig = LookRoleUserDialog.lookConfig;
      lookConfig.data = {
        name:item.name,
        key:item.key
      };
      this._dialog.open(LookRoleUserDialog,lookConfig);
    });

  }



}



