import {Component,OnInit,OnDestroy} from '@angular/core';
import {Router} from "@angular/router";
import {ConnectionService} from '../../core/services/connection.service';
import {LocalStorage} from '../../core/services/localstorage.service';
import {AppManageService} from './app-manage.service';
import {MdDialog,MdDialogRef} from '@angular/material';
import {AppDialog} from './dialogs/app.dialog';
import swal from 'sweetalert2';
@Component({
  selector: 'app-manage',
  templateUrl: './app-manage.component.html',
  styleUrls: ['./app-manage.component.less']
})
export class AppManageComponent implements OnInit,OnDestroy{
  private pageConfig:any = {
    totalItems:0,
    skips:[10,20,50,100],
  };
  private appList:any = [];
  private search:any = {
    currentPage:1,
    start:0,
    limit:10,
  };
  appDialog:MdDialogRef<AppDialog>;

  constructor(private _dialog:MdDialog,private service:AppManageService){
    this.appList = this.service.appList;
  }

  ngOnInit(){
    if(LocalStorage.get('appParams')){
      this.search = LocalStorage.get('appParams');
      this.getAppList();
    }else{
      this.getAppList();
    }
  }

  ngOnDestroy(){
    LocalStorage.remove('appParams');
  }

  /**
   *@desc 查询应用列表
   */
  getAppList(){
    this.search.start = this.search.limit * (this.search.currentPage-1);
    let params = {
      start:this.search.start,
      limit:this.search.limit
    }
    LocalStorage.set('appParams',this.search);
    this.service.reloadApp(params).subscribe(res=>{
      if(res.data.result){
        this.pageConfig.totalItems = res.data.result.totalSize;
      }
    },error=>{});
  }
  changeLimit(skip){
    this.search.currentPage = 1;
    this.search.limit = skip;
    this.getAppList();
  }

  addApp(){
    let appConfig = AppDialog.appConfig;
    appConfig.data.id = '';
    this.appDialog = this._dialog.open(AppDialog,appConfig);
    this.appDialog.afterClosed().subscribe(result=>{
      if(result){
        if(result != 'cancel'){
          delete result.id;
          this.service.createApp(result).subscribe(res=>{
            if(res.data.status===200){
              this.getAppList();
            }
          });
        }
      }
      AppDialog.appConfig.data = {};
    })
  }

  editApp(item,index){
    let appConfig = AppDialog.appConfig;
    appConfig.data.id = item.id;
    this.appDialog = this._dialog.open(AppDialog,appConfig);
    this.appDialog.afterClosed().subscribe(result=>{
      if(result){
        if(result != 'cancel'){
          this.service.editApp(item.id,result).subscribe(res=>{
            if(res.data.status===200){
              this.getAppList();
            }
          });
        }
      }
      AppDialog.appConfig.data = {};
    })
  }

  removeApp(item:any,index:any){
    swal({
      title: '您确定删除该应用吗?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: '是的, 删除!',
      cancelButtonText: '不, 再想想'
    }).then(
      ()=>{
        this.service.removeApp(item.id,index).subscribe(res=>{
          if(res.data.status === 200){
            this.getAppList();
          }
        });
      }, function(dismiss) {
        // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
        if (dismiss === 'cancel') {
        }
      });
  }
}



