import {Component,OnInit,OnDestroy,Inject,ElementRef} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {RoleManageService} from '../role-manage.service';
@Component({
  selector:'add-member',
  host: {
    "(document:click)": "collapse($event)",
  },
  templateUrl:'./add-member.dialog.html',
  styleUrls:['./add-member.dialog.less']
})

export class AddMemberDialog implements OnInit{
  static memberConfig: MdDialogConfig = {
    width: '470px',
    data: {}
  };
  private choose:boolean = false;
  private allUsers:any=[];
  private addUser:any = {};
  private roleUserList:any = [];
  private showUsers:any = [];
  ngOnInit(){
    this.allUsers.forEach(item=>{
      let index = this.roleUserList.findIndex(user=>user.id === item.id);
      if(index > -1){
        item.roleStatus = 1;
      }else{
        item.roleStatus = 0;
      }
    })
  }
  constructor(
    public dialogRef:MdDialogRef<AddMemberDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    public el: ElementRef,
    private service:RoleManageService
  ){
    this.allUsers = this.service.allUsers;
    this.roleUserList = this.service.roleUserList;
    this.showUsers.push.apply(this.showUsers,this.allUsers);
  }


  /**
   * @desc 是否显示用户列表
   */
  changChoose(){
    this.choose = !this.choose;
  }


  collapse() {
    // Checks to see if click is inside element; if not, collapse element
    if (!this.el.nativeElement.contains(event.target)) {
      this.choose = false;
    }
  }

  chooseUser(user:any){
    if(user.roleStatus === 1){
      return;
    }
    this.addUser = Object.assign(this.addUser,user);
    this.choose = false;
  }

  /**
   * @desc 为某角色添加用户
   */
  addMember(){
    let params = {roleId:this.data.currentRole.id,userId:this.addUser.id};
    this.service.addMember(params).subscribe(res=>{
      if(res.data.status == 200){
        this.data.getUserList();
      }
    });
    this.dialogRef.close('add');
  }

  /**
   * @desc 通过输入框内容同时筛选出符合条件的用户
   */
  filterMember(event){
    this.choose = true;
    this.showUsers = this.allUsers.filter(item=>item.username.indexOf(this.addUser.username) > -1);
  }
}



