import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {LocalStorage} from '../../core/services/localstorage.service';
import {Observable} from "rxjs";
@Injectable()
export class PasswordService {
  constructor(private connectionService:ConnectionService,private router:Router) {}

  findPass(params){
    let path = '/api/password/forget_password';
    const passObservable = Observable.fromPromise(this.connectionService.post(path,params));
    return passObservable;
  }

  modifyPass(user,params){
    let path = '/api/password/change_password?username='+user.username+'&key='+user.key;
    const passObservable = Observable.fromPromise(this.connectionService.put(path,params));
    return passObservable;
  }
}



