import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {AuthManageService} from '../auth-manage.service';
@Component({
  selector:'look-form',
  template:`
    <div class="main-form">
      <div class="look-title">查看角色和用户</div>
      <div class="content" style="margin-top:10px;">
         <h1>拥有“{{data.name}}({{data.key}})”权限的角色</h1>
         <ul>
           <li *ngFor="let item of authRoleList">
             <span *ngIf="item && item.id">{{item.name}}({{item.key}})</span>
           </li>
         </ul>
      </div>
      <div class="content" style="margin-top:20px;margin-bottom:10px;">
         <h1>拥有“{{data.name}}({{data.key}})”权限的用户</h1>
         <ul>
           <li  *ngFor="let item of authUserList">
             <span  *ngIf="item && item.id">
             <em *ngIf="item.departmentName">{{item?.departmentName}}-</em>{{item.fullName}}<em *ngIf="item.username">({{item.username}})</em>
             </span>
           </li>
         </ul>
      </div>
      <div style="clear:both;text-align:center;margin-top:24px;">
        <button md-button class="cancel" (click)="dialogRef.close('cancel')">关闭</button>
      </div>

    </div>
  `,
  styles:[`
    .main-form{
      position:relative;
    }
    .look-title{
      height:30px;
      line-height:30px;
      font-weight:700;
      text-align: center;
      font-size:18px;
      margin-bottom:14px;
    }
    .close{
      position:absolute;
      right:-18px;
      top:-28px;
    }
    .content h1{
      font-size:17px;
      font-weight:700;
      margin-bottom:10px;
    }
    .content ul{
      list-style:none;
      padding:0;
      margin:0;
    }
    .content ul li{
      height:25px;
      line-height:25px;
      font-size:12px;
      color:#888;
    }
    .content ul li em{
      font-style:normal;
      font-weight:500;
    }
     .cancel{
      color:#fff;
      font-size:13px;
      line-height:30px;
      background:#1194F6;
      min-width:120px !important;
    }
    .cancel:focus{
      outline:none;
    }
  `]
})

export class LookRoleUserDialog implements OnInit{
  static lookConfig: MdDialogConfig = {
    width: '600px',
    data: {}
  };

  private authUserList:any = [];
  private authRoleList:any = [];

  constructor(
    public dialogRef:MdDialogRef<LookRoleUserDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:AuthManageService
  ){
    this.authRoleList = this.service.authRoleList;
    this.authUserList = this.service.authUserList;
  }

  ngOnInit(){
    this.data.status = 1;
  }

  saveRole(data:any){
    this.dialogRef.close();
  }
}



