import {Component,OnInit,OnDestroy,ViewChild} from '@angular/core';
import {Router} from "@angular/router";
import {LocalStorage} from '../../../core/services/localstorage.service';
import {DaterangePickerComponent,DaterangepickerConfig} from 'ng2-daterangepicker';
import {LogManageService} from '../log-manage.service';
import * as moment from 'moment';
@Component({
  selector: 'login-log',
  templateUrl: './login-log.component.html',
  styleUrls: ['./login-log.component.less']
})
export class LoginLogComponent implements OnInit,OnDestroy{
  private pageConfig:any = {
    totalItems:0,
    skips:[10,20,50,100],
  };
  private appList:any = [];
  private search:any = {
    currentPage:1,
    start:0,
    limit:10,
    startTime:moment({hour :0, minute :0}),
    endTime:moment({hour :23, minute :59})
  };
  @ViewChild(DaterangePickerComponent)
  private picker: DaterangePickerComponent;
  constructor(
    private service:LogManageService,
    private daterangepickerOptions: DaterangepickerConfig,
  ){
    this.daterangepickerOptions.settings = {
      timePicker: true,
      timePicker24Hour: true,
      locale: {
        format: 'YYYY-MM-DD HH:mm' ,
        applyLabel:'确定',
        cancelLabel: '取消',
        daysOfWeek: ["日","一","二","三","四","五", "六"],
        monthNames: ["一月","二月","三月","四月","五月","六月","七月","八月","九月","十月","十一月","十二月"],
      },
      startDate: this.search.startTime,
      endDate: this.search.endTime,
      autoUpdateInput:true
    };
  }
  ngOnInit(){
    if(LocalStorage.get('loginParams')){
      this.search = LocalStorage.get('loginParams');
      this.daterangepickerOptions.settings.startDate = this.search.startTime;
      this.daterangepickerOptions.settings.endDate = this.search.endTime;
      this.getLoginLog();
    }else{
      this.getLoginLog();
    }
  }
  ngOnDestroy(){
    LocalStorage.remove('loginParams');
  }

  getLoginLog(){
    this.search.start = this.search.limit * (this.search.currentPage-1);
    LocalStorage.set('loginParams',this.search);

    if(this.search.userName){
      this.search.userName = this.search.userName.replace(/\s/g, "");
    }
    if(this.search.fullName){
      this.search.fullName = this.search.fullName.replace(/\s/g, "");
    }
    let query = Object.assign({},this.search);
    for(var key in query){
      if(query[key]!=0 && !query[key]){
        delete query[key];
      }
    }
    delete query.currentPage;
    this.service.retriveLoginLog(query).subscribe(res=>{
      if(res.data.result){
        this.pageConfig.totalItems = res.data.result.totalSize;
      }
    },error=>{
      console.log(error);
    })
  }


  /**
   * @desc 选择开始和结束通话时间
   * @param value - 选择时间段后传入的值
   */
  selectedDate(value: any) {
    this.search.startTime = value.start.format('YYYY-MM-DD HH:mm');
    this.search.endTime = value.end.format('YYYY-MM-DD HH:mm');
  }

  /**
   * @desc 选择关键词分类后查询该分类下的关键字
   * @param group - 关键词分类的id
   */
  searchByKey(event:any){
    let keyCode=event ? event.keyCode : event.which;
    if(keyCode===13){
      this.getLoginLog();
    }
  }

  changeLimit(skip){
    this.search.currentPage = 1;
    this.search.limit = skip;
    this.getLoginLog();
  }
}



