import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {LocalStorage} from '../../core/services/localstorage.service';
import {Observable} from "rxjs";
@Injectable()
export class SystemSetService {
  @observable systemEmail:any = {};
  @observable operaLogList:any = [];

  constructor(private connectionService:ConnectionService){}
  getSystemEmail(){
    let path = '/api/setting';
    this.connectionService.get(path).then(res=>{
      if(res.data.status == 200){
        Object.assign(this.systemEmail,res.data.result);
      }
    }).catch(error=>{console.log(error);});

  }

  editSystemEmail(params){
    let path = '/api/setting';
    this.connectionService.post(path,params).then((res:any)=>{
      if(res.data.status == 200){
        Object.assign(this.systemEmail,res.data.result);
      }
    }).catch(error=>{console.log(error);});
  }

}



