import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {LocalStorage} from '../../core/services/localstorage.service';
import {SwalService} from '../../core/services/swal.service';
import {Observable} from "rxjs";
@Injectable()
export class AppManageService {
  @observable appList:any = [];
  constructor(private connectionService:ConnectionService,private router:Router,private swal:SwalService) {}

  reloadApp(params){
    let path = '/api/application';
    const appsObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    appsObservable.subscribe((page: any) => {
      this.appList.length = 0;
      this.appList.push.apply(this.appList, page.data.result.pageData);
    },error=>{
    });
    return appsObservable;
  }

  /**
   * @desc 添加应用
   * @param params - 添加的应用参数
   */
  createApp(params:any){
    let path = '/api/application';
    const appObservable = Observable.fromPromise(this.connectionService.post(path,params));
    appObservable.subscribe((res:any) => {
      if(res.data.status!=200){
        let message = res.data.error || '该应用添加失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','该应用添加失败，请重试！');
    })
    return appObservable;
  }


  /**
   * @desc 修改应用
   * @param params - 添加的应用参数
   */
  editApp(id,params:any){
    let path = '/api/application/' + id;
    const appObservable = Observable.fromPromise(this.connectionService.put(path,params));
    appObservable.subscribe((res:any) => {
      if(res.data.status!=200){
        let message = res.data.error || '该应用修改失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','该应用修改失败，请重试！');
    })
    return appObservable;
  }


  /**
   * @desc 删除应用
   * @param id - 删除的应用id
   * @param index - 被删除的应用在应用列表中的索引
   */
  removeApp(id,index){
    let path = '/api/application/'+id;
    const appObservable = Observable.fromPromise(this.connectionService.delete(path));
    appObservable.subscribe(res=>{
     if(res.data.status!=200){
        let message = res.data.error || '该应用删除失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error=>{
      let message = '该应用删除失败，请稍后重试！';
      this.swal.hint('error',message);
    });
    return appObservable;
  }
}



