import {Component,OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {ConnectionService} from '../../../core/services/connection.service';
import {LocalStorage} from '../../../core/services/localstorage.service';
import {PasswordService} from '../password.service';
import {SwalService} from '../../../core/services/swal.service';
import swal from 'sweetalert2';

@Component({
  selector: 'password-modify',
  templateUrl: './password-modify.component.html',
  styleUrls: ['./password-modify.component.less']
})
export class PasswordModifyComponent{
  public user: any = {};
  constructor(private service:PasswordService,private swal:SwalService,private router:Router){}
  modify(form:any){
    let find = window.location.href;
    let key=this.getUrlParam(find,'key');
    let username = this.getUrlParam(find,'username');
    let user:any = {};
    if(key&&key.length>0){
      user.key=key;
    }
    if(username&&username.length>0){
      user.username=username;
    }
    this.service.modifyPass(user,form).subscribe(res=>{
      if(res.data.status == 200){
        swal({
          title: '密码修改成功，点击确定跳转至登录页面',
          type: 'success',
          showCancelButton: false,
          confirmButtonText: '确定',
        }).then(
          ()=>{
            this.router.navigate(['/index']);
          }, function(dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
            }
          });
      }else{
        let msg = res.data.error || '密码修改失败，请稍后重试！';
        this.swal.hint('error',msg);
      }
    },error=>{
      let msg = '密码修改失败，请稍后重试！';
      this.swal.hint('error',msg);
    })
  }
  verifyResult(){
    return this.user.password && this.user.repassword && this.user.password == this.user.repassword;
  }


  getUrlParam(sUrl, sKey) {

    var param = sUrl.split('#')[0].split('?')[1];
    if(param){
      if (sKey){//指定参数名称
        var strs = param.split('&');
        var arrs = new Array();//如果存在多个同名参数，则返回数组
        for(var i = 0, len = strs.length; i < len; i++){
          var tmp = strs[i].split('=');
          if(tmp[0] == sKey){
            arrs.push(tmp[1]);
          }
        }
        if (arrs.length == 1){//返回该参数的值或者空字符串
          return arrs[0];
        } else if (arrs.length == 0){
          return "";
        } else {
          return arrs;
        }
      } else {//不指定参数名称，返回全部的参数对象 或者 {}
        if(param == undefined || param == ""){
          return {};
        } else {
          var strs = param.split('&');
          var arrObj = new Object();
          for(var i = 0, len = strs.length; i < len; i++){
            var tmp = strs[i].split('=');
            if (!(tmp[0] in arrObj)) {
              arrObj[tmp[0]] = [];
            }
            arrObj[tmp[0]].push(tmp[1]);
          }
          return arrObj;
        }
      }
    }else{
      return '';
    }
  }
}



