import {Component,Input,Output,OnInit,EventEmitter,enableProdMode} from '@angular/core';
import {MdSelectModule} from '@angular/material';
import {UserManageService} from './user-manage.service';
import {MdDialog} from '@angular/material';
import {UserRoleDialog} from './dialogs/user-role.dialog';
import {UserFormDialog} from './dialogs/user-form.dialog';
import swal from 'sweetalert2';
enableProdMode();
@Component({
  selector:'user-list',
  templateUrl:'user-list.component.html',
  styles:[``]
})
export class UserListComponent{
  @Input() getUserList;
  @Input() offset;
  private userList;
  private departmentList:any = [];
  constructor(private service:UserManageService,private _dialog:MdDialog){
    this.userList = this.service.userList;
    this.departmentList = this.service.departmentList;
  }


  /**
   * @desc 删除用户
   * @param item - 用户信息
   */
  removeUser(item:any,index:any){
    swal({
      title: '您确定删除该用户吗?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: '是的, 删除!',
      cancelButtonText: '不, 再想想'
    }).then(
      ()=>{
        this.service.removeUser(item.id,index).subscribe(res=>{
          if(res.data.status == 200){
            this.getUserList();
          }
        });
      }, function(dismiss) {
      // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
      if (dismiss === 'cancel') {
      }
    });
  }


  /**
   * @desc 编辑用户角色
   * @param item - 用户信息
   */
  editRole(item:any){
    this.service.getUserDetail(item.id).subscribe(res=>{
      let roleConfig = UserRoleDialog.roleConfig;
      roleConfig.data = Object.assign({},res.data.result);
      roleConfig.data.getUserList = this.getUserList;
      this._dialog.open(UserRoleDialog,roleConfig);
    })
  }

  /**
   * @desc 编辑用户信息
   * @param item - 被编辑的用户
   */
  editUser(item:any,index:any){
    this.service.getUserDetail(item.id).subscribe(res=>{
      let formConfig = UserFormDialog.formConfig;
      formConfig.data = Object.assign({},res.data.result);
      formConfig.data.departmentList = this.departmentList;
      formConfig.data.getUserList = this.getUserList;
      formConfig.data.index = index;
      this._dialog.open(UserFormDialog,formConfig);
    })
  }


}



