import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {IndexComponent} from './core/index.component';
import {AuthGuard} from "./core/services/auth-guard.service";
import {AppHomeComponent} from "./login-manage/app-home.component";
import {AppManageComponent} from "./login-manage/app-manage/app-manage.component";
import {AuthManageComponent} from "./login-manage/auth-manage/auth-manage.component";
import {LoginLogComponent} from "./login-manage/log-manage/login-log/login-log.component";
import {OperationLogComponent} from "./login-manage/log-manage/operation-log/operation-log.component";
import {PasswordFindComponent} from "./login-manage/password-manage/password-find/password-find.component";
import {PasswordModifyComponent} from "./login-manage/password-manage/password-modify/password-modify.component";
import {RoleManageComponent} from "./login-manage/role-manage/role-manage.component";
import {SystemSetComponent} from "./login-manage/system-set/system-set.component";
import {UserManageComponent} from "./login-manage/user-manage/user-manage.component";
import {ManageUserComponent} from './login-manage/role-manage/manage-user.component';
import {LogManageComponent} from './login-manage/log-manage/log-manage.component';
const routes:Routes = [
  {
    path:'',redirectTo:'/index/app', pathMatch: 'full'
  },
  {
    path:'index',component:IndexComponent,
    canActivate: [AuthGuard],
    children:[
      {path:'',redirectTo:'app',pathMatch:'full'},
      {path:'app',component:AppManageComponent},
      {path:'user',component:UserManageComponent},
      {path:'auth',component:AuthManageComponent},
      {path:'role',component:AppHomeComponent,
        children:[
          {path:'',redirectTo:'list',pathMatch:'full'},
          {path:'list',component:RoleManageComponent},
          {path:'user_manage/:roleId',component:ManageUserComponent},
        ]
      },
      {path:'log_manage',component:LogManageComponent,
        children:[
          {path:'',redirectTo:'login_log',pathMatch:'full'},
          {path:'login_log',component:LoginLogComponent},
          {path:'operation_log',component:OperationLogComponent},
        ]
      },
      {path:'password_find',component:PasswordFindComponent},
      //{path:'password_modify',component:PasswordModifyComponent},
      {path:'system_set',component:SystemSetComponent}
    ]
    //children:[
    //  {path:'',redirectTo:'tsso-auth',pathMatch:'full'},
    //
    //  {path:'tsso-auth',component:AppHomeComponent,
    //    children:[
    //      {path:'',redirectTo:'app',pathMatch:'full'},
    //      {path:'app',component:AppManageComponent},
    //      {path:'user',component:UserManageComponent},
    //      {path:'auth',component:AuthManageComponent},
    //      {path:'role',component:AppHomeComponent,
    //        children:[
    //          {path:'',redirectTo:'list',pathMatch:'full'},
    //          {path:'list',component:RoleManageComponent},
    //          {path:'user_manage/:roleId',component:ManageUserComponent},
    //        ]
    //      },
    //      {path:'log_manage',component:LogManageComponent,
    //        children:[
    //          {path:'',redirectTo:'login_log',pathMatch:'full'},
    //          {path:'login_log',component:LoginLogComponent},
    //          {path:'operation_log',component:OperationLogComponent},
    //        ]
    //      },
    //      {path:'password_find',component:PasswordFindComponent},
    //      //{path:'password_modify',component:PasswordModifyComponent},
    //      {path:'system_set',component:SystemSetComponent}
    //    ]
    //  },
    //  //{path:'app_manage',component:AppHomeComponent,
    //  //  children:[
    //  //    {path:'',redirectTo:'auth',patchMatch:'full'},
    //  //    {path:'auth',component:AuthManageComponent}
    //  //  ]
    //  //}
    //]
  },

];


@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
  providers: [

  ]
})

export class AppRoutingModule { }



