import {Component,OnInit,OnDestroy} from '@angular/core';
import {Router} from "@angular/router";
import {LocalStorage} from '../../core/services/localstorage.service';
import {MdDialog} from '@angular/material';
import {UserFormDialog} from './dialogs/user-form.dialog';
import {UserManageService} from './user-manage.service';
@Component({
  selector: 'user-manage',
  templateUrl: './user-manage.component.html',
  styleUrls: ['./user-manage.component.less']
})
export class UserManageComponent implements OnInit,OnDestroy{
  private pageConfig:any = {
    totalItems:0,
    skips:[10,20,50,100],
  };
  private search:any = {
    currentPage:1,
    start:0,
    limit:10,
    roleId:'all'
  };
  private roleList:any = [];
  private departmentList:any = [];
  constructor(private _dialog:MdDialog,private service:UserManageService){
    this.roleList = this.service.roleList;
    this.departmentList = this.service.departmentList;
  }

  ngOnInit(){
    if(LocalStorage.get('userParams')){
      this.search = LocalStorage.get('userParams');
      this.getUserList();
    }else{
      this.getUserList();
    }
    this.service.reloadRoleList();
    this.service.reloadDepartment();
  }

  ngOnDestroy(){
    LocalStorage.remove('userParams');
  }

  getUserList = (start?:any)=>{
    if(start == 0){
      this.search.currentPage = 1;
    }
    this.search.start = this.search.limit * (this.search.currentPage-1);
    LocalStorage.set('userParams',this.search);
    if(this.search.username){
      this.search.username = this.search.username.replace(/\s/g, "");
    }
    if(this.search.fullName){
      this.search.fullName = this.search.fullName.replace(/\s/g, "");
    }
    let query = Object.assign({},this.search);
    for(var key in query){
      if(query[key]!=0 && !query[key]){
        delete query[key];
      }
    }
    delete query.currentPage;
    if(query.roleId == 'all'){
      delete query.roleId;
    }
    this.service.retriveUserList(query).subscribe(res=>{
      if(res.data.result){
        this.pageConfig.totalItems = res.data.result.totalSize;
      }
    },error=>{
    })
  }


  changeLimit(skip){
    this.search.currentPage = 1;
    this.search.limit = skip;
    this.getUserList();
  }

  addUser(){
    let addDialog = UserFormDialog.formConfig;
    addDialog.data = {
      id:'',
      departmentList:this.departmentList,
      getUserList:this.getUserList,
      status:1
    }
    if(this.departmentList[0] && this.departmentList[0].id){
      addDialog.data.departmentId = this.departmentList[0].id;
    }
    this._dialog.open(UserFormDialog,addDialog);
  }

  /**
   * @desc 选择关键词分类后查询该分类下的关键字
   * @param group - 关键词分类的id
   */
  searchByKey(event:any){
    let keyCode=event ? event.keyCode : event.which;
    if(keyCode===13){
      this.getUserList();
    }
  }
}



