import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Observable} from 'rxjs';
import {SwalService} from '../../core/services/swal.service';

@Injectable()
export class RoleManageService {
  //被查询的角色列表
  @observable roleList:any = [];

  @observable allRoles:any = [];

  //角色拥有的用户列表
  @observable roleUserList:any = [];

  //所有用户列表
  @observable allUsers:any = [];

  @observable roleAuthList:any = [];

  @observable authList:any = [];
  @observable authTypeList:any = [];
  @observable groupValue:any = [];
  @observable permissionIdList:any = [];

  constructor(private connectionService:ConnectionService,private swal:SwalService) {

  }


  /*------------------------------角色的增删查------------------------------------------*/

  /**
   * @desc 查询所有角色
   * @param params - 查询条件信息
   */
  reloadAllRoles(){
    let path = '/api/role';
    let param = {start:0,limit:999999};
    this.connectionService.get(path,{params:param}).then((res:any)=>{
      this.allRoles.length = 0;
      this.allRoles.push.apply(this.allRoles, res.data.result.pageData);
    }).catch(error=>{})

  }

  /**
   * @desc 查询角色列表
   * @param params - 查询条件信息
   */
  retriveRoleList(params){
    let path = '/api/role';
    const roleObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    roleObservable.subscribe(res=>{
        this.roleList.length = 0;
        this.roleList.push.apply(this.roleList, res.data.result.pageData);
    },error=>{
    });
    return roleObservable;
  }

  /**
   * @desc 新增角色
   * @param params - 角色信息
   */
  createRole(params:any){
    let path = '/api/role';
    const roleObservable = Observable.fromPromise(this.connectionService.post(path,params));
    roleObservable.subscribe(res => {
      if(res.data.status!=200){
        let message = res.data.error || '角色新增失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','角色新增失败，请重试！');
    });
    return roleObservable;
  }


  /**
   * @desc 修改角色
   * @param params - 角色信息
   */
  editRole(id,params:any){
    let path = '/api/role/'+id;
    const roleObservable = Observable.fromPromise(this.connectionService.put(path,params));
    roleObservable.subscribe(res => {
      if(res.data.status!=200){
        let message = res.data.error || '角色修改失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','角色修改失败，请重试！');
    });
    return roleObservable;
  }

  /**
   * @desc 删除角色
   * @param id - 被删除的角色id
   * @param index - 被删除的角色在角色列表中的索引
   */
  removeRole(id:any,index){
    let path = '/api/role/'+id;
    const roleObservable = Observable.fromPromise(this.connectionService.delete(path));
    roleObservable.subscribe(res=>{
     if(res.data.status != 200){
        let message = res.data.error || '该角色删除失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error=>{
      let message = '该角色删除失败，请稍后重试！';
      this.swal.hint('error',message);
    })
    return roleObservable;
  }


  /*------------------------------end------------------------------------------*/



  /*------------------------------角色用户的增删查------------------------------------------*/
  /**
   * @desc 查询所有用户列表
   */
  reloadAllUsers(){
    let path = '/api/user';
    let param = {start:0,limit:999999};
    this.connectionService.get(path,{params:param}).then((res:any)=>{
      this.allUsers.length = 0;
      this.allUsers.push.apply(this.allUsers,res.data.result.pageData);

    }).catch(error=>{})
  }


  /**
   * @desc 查询某角色拥有的用户列表
   * @param params - 查询参数
   */
  retriveRoleUser(params){
    this.roleUserList.length = 0;
    let path = '/api/user';
    this.connectionService.get(path,{params:params}).then((res:any)=>{
      this.roleUserList.push.apply(this.roleUserList,res.data.result.pageData);
    }).catch(error=>{})
  }



  addMember(params:any){
    let path = '/api/role/user';
    let memberObservable = Observable.fromPromise(this.connectionService.post(path,params));
    memberObservable.subscribe(res=>{
      if(res.data.status!=200){
        let message = res.data.error || '该应用添加失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error=>{
      this.swal.hint('error','该应用添加失败，请重试！');
    });
    return memberObservable;
  }

  /*------------------------------end------------------------------------------*/


  /*------------------------------角色权限的增删改查------------------------------------------*/

  /**
   * @desc 查询所有权限列表
   * @param params - 查询列表需要的请求参数
   */
  reloadAuthList(params){
    let path = '/api/permission/';
    this.connectionService.get(path,{params:params}).then(
      (page:any)=>{
        this.authList.length = 0;
        this.authList.push.apply(this.authList,page.data.result.pageData);
      }
    ).catch(error=>{})
  }


  /**
   * @desc 查询某角色拥有的权限列表
   * @param params - 查询列表需要的请求参数
   */
  retriveRoleAuth(params){
    let path = '/api/permission/';
    const roleAuthObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    roleAuthObservable.subscribe(page=>{
      this.roleAuthList.length = 0;
      this.roleAuthList.push.apply(this.roleAuthList, page.data.result.pageData);
      this.authList.forEach(item => {
        let index = this.roleAuthList.findIndex(role => role.id === item.id);
        if(index > -1){
          item.selected = true;
        }else{
          item.selected = false;
        }
        if(this.groupValue.findIndex(group => group.applicationKey === item.applicationKey) === -1){
          this.groupValue.push({applicationKey:item.applicationKey,applicationName:item.applicationName});
        }
      });
      this.permissionIdList.length = 0;
      this.roleAuthList.forEach(item=>{
        this.permissionIdList.push(item.id);
      });
    },error=>{})
    return roleAuthObservable;
  }



  /**
   * @desc 更新角色权限
   * @param id - 角色id
   * @param authArray - 角色权限id数组
   */
  editRoleAuth(id,authArray){
    let path = '/api/role/' + id + '/permission';
    this.connectionService.put(path,authArray).then(res=>{
      if(res.data.status!=200){
        let message = res.data.error || '角色权限修改失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    }).catch(error=>{
      let message = '角色权限修改失败，请稍后重试！';
      this.swal.hint('error',message);
    })
  }

  /*------------------------------end------------------------------------------*/

}



