import {Component,OnInit,Inject} from '@angular/core';
import {MdDialogRef,MdDialogConfig,MD_DIALOG_DATA} from '@angular/material';
import {AuthManageService} from '../auth-manage.service';
@Component({
  selector:'auth-form',
  templateUrl:'./auth.dialog.html',
  styleUrls:['./auth.dialog.less']
})

export class AuthDialog{
  static authConfig: MdDialogConfig = {
    width: '600px',
    data: {}
  };

  constructor(
    public dialogRef:MdDialogRef<AuthDialog>,
    @Inject(MD_DIALOG_DATA) public data:any,
    private service:AuthManageService
  ){

  }
  saveAuth(data:any){
    let params = Object.assign({},data);
    delete params.getAuthList;
    this.service.createAuth(params).subscribe(res=>{
      if(res.data.status == 200){
        this.data.getAuthList(0);
      }
    });
    this.dialogRef.close(data);
  }
}



