import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {Observable} from 'rxjs';
import {SwalService} from '../../core/services/swal.service';
import {LocalStorage} from '../../core/services/localstorage.service';
@Injectable()
export class UserManageService {
  @observable userList:any = [];
  @observable roleList:any = [];
  @observable userRoleList:any = [];
  @observable departmentList:any = [];
  constructor(private connectionService:ConnectionService,private router:Router,private swal:SwalService) {}
  /**
   * @desc 查询角色列表
   */
  reloadRoleList(){
    let path = '/api/role';
    let param = {start:0,limit:999999};
    this.connectionService.get(path,{params:param}).then((res:any)=>{
      this.roleList.length = 0;
      this.roleList.push.apply(this.roleList,res.data.result.pageData);
    }).catch(error=>{})
  }


  /**
   * @desc 查询部门列表
   */
  reloadDepartment(){
    let path = '/api/department';
    this.connectionService.get(path).then((res:any)=>{
      this.departmentList.length = 0;
      this.departmentList.push.apply(this.departmentList,res.data.result.pageData);
    }).catch(error=>{})
  }

  /**
   * @desc 查询用户列表
   * @param params - 查询条件信息
   */
  retriveUserList(params){
    let path = '/api/user';
    const userObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    userObservable.subscribe(res=>{
      this.userList.length = 0;
      this.userList.push.apply(this.userList, res.data.result.pageData);
    },error=>{});
    return userObservable;
  }


  /**
   * @desc 新增用户
   * @param params - 用户信息
   */
  createUser(params:any){
    let path = '/api/user';
    const userObservable = Observable.fromPromise(this.connectionService.post(path,params));
    userObservable.subscribe((res:any) => {
      if(res.data.status!=200){
        let message = res.data.error || '用户新增失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','用户新增失败，请重试！');
    });
    return userObservable;
  }

  /**
   * @desc 编辑用户信息
   * @param id - 被修改的用户id
   * @param index - 被修改的用户在用户列表中的索引
   * @param params - 被修改的用户信息
   */
  editUser(id:any,index:any,params:any){
    let path = '/api/user/'+id;
    const userObservable = Observable.fromPromise(this.connectionService.put(path,params));
    userObservable.subscribe((res:any) => {
      if(res.data.status!=200){
        let message = res.data.error || '用户修改失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','用户修改失败，请重试！');
    });
    return userObservable;
  }


  /**
   * @desc 删除用户
   * @param id - 被删除的用户id
   * @param index - 被删除的用户在用户列表中的索引
   */
  removeUser(id:any,index){
    let path = '/api/user/'+id;
    const userObservable = Observable.fromPromise(this.connectionService.delete(path));
    userObservable.subscribe(res=>{
      if(res.data.status!=200){
        let message = res.data.error || '该用户删除失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error=>{
      let message = '该用户删除失败，请稍后重试！';
      this.swal.hint('error',message);
    });
    return userObservable;
  }


  /**
   * @desc 查询用户详情
   * @param id - 被查询的用户id
   */
  getUserDetail(id){
    let path = '/api/user/'+id;
    this.userRoleList.length = 0;
    const userDetailObservable = Observable.fromPromise(this.connectionService.get(path));
    userDetailObservable.subscribe(res=>{
      this.userRoleList.push.apply(this.userRoleList,res.data.result.roleList);
    },error=>{

    });
    return userDetailObservable;
  }

  /**
   * @desc 修改用户角色
   * @param id - 被修改的用户id
   * @param roleArray - 用户角色数组
   */
  editUserRole(id,roleArray:any){
    let path = '/api/user/' + id + '/role';
    this.connectionService.put(path,roleArray).then(res=>{
      if(res.data.status!=200){
        let message = res.data.error || '用户角色修改失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    }).catch(error=>{
      let message = '用户角色修改失败，请稍后重试！';
      this.swal.hint('error',message);
    })
  }
}



