import {Component,OnInit,OnDestroy} from '@angular/core';
import {RoleManageService} from './role-manage.service';
import {AddMemberDialog} from './dialogs/add-member.dialog';
import {MdDialog} from '@angular/material';
import {Router,ActivatedRoute} from '@angular/router';
import {LocalStorage} from "../../core/services/localstorage.service";
@Component({
  selector:'role-manage-user',
  templateUrl:'./manage-user.component.html',
  styleUrls:['./manage-user.component.less']
})

export class ManageUserComponent implements OnInit,OnDestroy{
  private search:any={start:0,limit:999999};
  private roleUserList:any;
  private selectedItems:any = [];
  private checkAll:boolean = false;
  private allRoles:any = [];
  constructor(private service:RoleManageService,
              private _dialog:MdDialog,
              private router:Router,private activeRoute:ActivatedRoute){
    this.roleUserList = this.service.roleUserList;
    this.allRoles = this.service.allRoles;
  }

  ngOnInit(){
    this.service.reloadAllUsers();
    this.service.reloadAllRoles();
    if( LocalStorage.get('roleUser')){
      this.search = LocalStorage.get('roleUser');
      this.getUserList();
    }else{
      this.search.roleId = parseInt(this.activeRoute.snapshot.params.roleId);
      this.getUserList();
    }
  }

  ngOnDestroy(){
    LocalStorage.remove('roleUser');
  }


  /**
   * @desc 查询用户列表
   */
  getUserList = ()=>{
    if(this.search.username){
      let username = this.search.username.replace(/\s/g, "");
      if(username.length == 0){
        delete this.search.username;
      }
    }
    LocalStorage.set('roleUser',this.search);
    this.service.retriveRoleUser(this.search);
  }

  allSelect(){
    return this.selectedItems.length > 0 && this.selectedItems.length == this.roleUserList.length;
  }

  setAllSelect(checked:any){
    this.selectedItems = [];
    this.checkAll =checked;
    this.roleUserList.forEach(item=>{
      item.selected = checked;
      if(checked){
        this.selectedItems.push(item);
      }
    })
  }

  someSelect(){
    return this.selectedItems.length > 0 && this.selectedItems.length < this.roleUserList.length;
  }

  /**
   * @desc 选择一个
   */
  selectOneItem(item,checked){
    item.selected = checked;
    const index = this.selectedItems.findIndex(it=>it.id === item.id);
    if(checked){
      if(index === -1){
        this.selectedItems.push(item);
      }
    }else{
      if(index > -1){
        this.selectedItems.splice(index,1);
      }
    }
  }

  addUser(){
    let memberConfig = AddMemberDialog.memberConfig;
    let current = this.allRoles.filter(item=>item.id === this.search.roleId);
    memberConfig.data.currentRole = current[0];
    memberConfig.data.getUserList = this.getUserList;
    this._dialog.open(AddMemberDialog,memberConfig);
  }

  back(){
    this.router.navigate(['/index/role'])
  }

  /**
   * @desc 根据用户名查询用户
   */
  searchByName(event){
    let keyCode=event ? event.keyCode : event.which;
    if(keyCode===13){
      this.getUserList();
    }
  }


}



