import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import {CustomizeMaterialModule} from "./module/customize-material.module";
import {MobxAngularModule} from 'mobx-angular';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {IndexComponent} from "./core/index.component"
import {SharedModule} from "./shared/shared.module";
import { AppComponent } from './app.component';
import {AppRoutingModule } from './app-routing.module';
import {CoreModule } from './core/core.module';
import {LogManageModule} from './login-manage/log-manage/log-manage.module';
import {LoginManageModule} from './login-manage/login-manage.module';
import 'hammerjs';
import 'rxjs';
import '@angular/material/@angular/material.js'


@NgModule({
  declarations: [
    AppComponent,
    IndexComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    CustomizeMaterialModule,
    MobxAngularModule,
    CoreModule,
    SharedModule,
    LoginManageModule,
    LogManageModule,
    NgbModule.forRoot(),
    AppRoutingModule,
  ],
  providers: [

  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



