import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {PasswordModifyComponent} from "./password-modify/password-modify.component";
import {PasswordFindComponent} from "./password-find/password-find.component";
const clientRoutes: Routes = [
  { path: 'password_modify', component: PasswordModifyComponent },
  { path: 'password_find', component: PasswordFindComponent }

];

@NgModule({
  imports: [
    RouterModule.forChild(clientRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class ClientAuthRoutingModule {}



