import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { FormsModule }    from '@angular/forms';
import {RouterModule} from "@angular/router";
import {AuthManageComponent} from './auth-manage.component';
import {AuthManageService} from './auth-manage.service';
import {AuthDialog} from './dialogs/auth.dialog';
import { MaterialModule } from '@angular/material';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {AuthListComponent} from './auth-list.component';
import {LookRoleUserDialog} from './dialogs/look-role-user.dialog';
@NgModule({
  imports: [
    MaterialModule,
    NgbModule,
    CommonModule,
    FormsModule,
    RouterModule
  ],
  declarations: [
    AuthManageComponent,
    AuthDialog,
    AuthListComponent,
    LookRoleUserDialog
  ],
  entryComponents: [
    AuthDialog,
    LookRoleUserDialog
  ],
  exports:[
  ],
  providers: [AuthManageService]
})
export class AuthManageModule {}



