import {Component,OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {LocalStorage} from '../../../core/services/localstorage.service';
import {PasswordService} from '../password.service';
import {SwalService} from '../../../core/services/swal.service';
@Component({
  selector: 'password-find',
  templateUrl: './password-find.component.html',
  styleUrls: ['./password-find.component.less']
})
export class PasswordFindComponent{
  private account = '';
  private send:boolean = false;
  private timer:any;
  private countdown:any = 60;
  private sendMessage:any = {message :'发送给我',disabled: false};
  constructor(private service:PasswordService,private swal:SwalService,private router:Router){}
  sendClick(){
    let params = {username:this.account};
    this.service.findPass(params).subscribe(res=>{
      if(res.data.status == 200){
        this.send = true;
        this.sendAuthCodeInterval();
      }else{
        let msg = res.data.error || '密码找回失败，请稍后重试！';
        this.swal.hint('error',msg);
      }
    },error=>{
      let msg = '密码找回失败，请稍后重试！';
      this.swal.hint('error',msg);
    });
  }

  cancel(){
   this.router.navigate(['/index']);
  }

  sendAuthCodeInterval() {
    this.timer = setInterval(() => {
      if(this.countdown == 0) {
        this.sendMessage.message = '发送给我';
        this.countdown = 60;
        clearInterval(this.timer);
        this.sendMessage.disabled = false;
      }else {
        this.countdown --;
        this.sendMessage.message = "重新发送 (" + this.countdown + ")";
        this.sendMessage.disabled = true;
      }
    },1000)
  }

}



