import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { FormsModule }    from '@angular/forms';
import {RouterModule} from "@angular/router";
import { MaterialModule } from '@angular/material';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {RoleManageComponent} from './role-manage.component';
import {RoleListComponent} from './role-list.component';
import {RoleManageService} from './role-manage.service';
import {RoleDialog} from './dialogs/role.dialog';
import {ManageUserComponent} from './manage-user.component';
import {AddMemberDialog} from './dialogs/add-member.dialog';
import {RoleManageDialog} from './dialogs/role-manage.dialog';
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    MaterialModule,
    NgbModule,
    RouterModule
  ],
  declarations: [
    RoleManageComponent,
    RoleListComponent,
    RoleDialog,
    ManageUserComponent,
    AddMemberDialog,
    RoleManageDialog
  ],
  entryComponents: [
    RoleDialog,
    AddMemberDialog,
    RoleManageDialog
  ],
  exports:[
  ],
  providers: [
    RoleManageService
  ]
})
export class RoleManageModule {}



