import {Injectable} from '@angular/core';
import {observable, action } from 'mobx-angular';
import {ConnectionService} from '../../core/services/connection.service';
import {Router} from '@angular/router';
import {LocalStorage} from '../../core/services/localstorage.service';
import {SwalService} from '../../core/services/swal.service';
import {Observable} from "rxjs";
@Injectable()
export class AuthManageService {
  @observable appList:any = [];
  @observable authList:any = [];
  @observable roleList:any = [];
  @observable authRoleList:any = [];
  @observable authUserList:any = [];
  constructor(private connectionService:ConnectionService,
              private router:Router,
              private swal:SwalService) {}

  reloadApp(){
    let path = '/api/application';
    this.connectionService.get(path).then((res:any)=>{
      this.appList.length = 0;
      this.appList.push.apply(this.appList,res.data.result.pageData);
    },error=>{
    })
  }

  /**
   * @desc 查询权限列表
   * @param params - 查询条件信息
   */
  retriveAuthList(params){
    let path = '/api/permission/';
    const authObservable = Observable.fromPromise(this.connectionService.get(path,{params:params}));
    authObservable.subscribe(res=>{
      this.authList.length = 0;
      this.authList.push.apply(this.authList, res.data.result.pageData);
    },error=>{

    });
    return authObservable;
  }

  /**
   * @desc 新增权限
   * @param params - 权限信息
   */
  createAuth(params:any){
    let path = '/api/permission';
    const authObservable = Observable.fromPromise(this.connectionService.post(path,params));
    authObservable.subscribe(res => {
      if(res.data.status!=200){
        let message = res.data.error || '权限新增失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error => {
      this.swal.hint('error','权限新增失败，请重试！');
    })
    return authObservable
  }

  /**
   * @desc 删除权限
   * @param id - 被删除的权限id
   * @param index - 被删除的权限在权限列表中的索引
   */
  removeAuth(id:any,index){
    let path = '/api/permission/'+id;
    const authObservable = Observable.fromPromise(this.connectionService.delete(path));
    authObservable.subscribe(res=>{
      if(res.data.status!=200){
        let message = res.data.error || '该权限删除失败，请稍后重试！';
        this.swal.hint('error',message);
      }
    },error=>{
      let message = '该权限删除失败，请稍后重试！';
      this.swal.hint('error',message);
    });
    return authObservable;
  }

  /**
   * @desc 查看某权限的角色与用户
   * @param id - 权限的id
   */
  retirveAuthRoleUser(id){
    let userPath = '/api/permission/' + id + '/user';
    let rolePath = '/api/permission/' + id + '/role';
    const roleObservable = this.connectionService.get(rolePath).then(res=>{
      this.authRoleList.length = 0;
      this.authRoleList.push.apply(this.authRoleList,res.data.result);
    });
    const userObservable = this.connectionService.get(userPath).then(res=>{
      this.authUserList.length = 0;
      this.authUserList.push.apply(this.authUserList,res.data.result);
    })
    const allObservable =  Observable.fromPromise(Promise.all([roleObservable,userObservable]));
    return allObservable;
  }


}



