import {Component,Input,OnInit,Output,EventEmitter,enableProdMode} from '@angular/core';
import {MdSelectModule} from '@angular/material';
import {Router} from "@angular/router";
enableProdMode();
@Component({
  selector:'log-manage',
  template:`
<div class="manage-container" [ngClass]="{hidden_menu:isShow == false}">
    <second-menu [title]="title" [menu]="menu" (menuHide)="hideMenu($event)"></second-menu>
    <router-outlet></router-outlet>
</div>
  `,
  styles:[`
  .manage-container{
  height:100%;
  position:relative;
  padding-left:200px;
  -webkit-transition: all 0.2s ease;
}
.hidden_menu{
  padding-left:20px !important;
}
  `]
})
export class LogManageComponent implements OnInit{
  title:string = '日志管理';
  menu:any[] = [
    {id:1,name:'登录日志',choosed:true,link:'/index/log_manage/login_log'},
    {id:2,name:'操作日志',choosed:false,link:'/index/log_manage/operation_log'}
  ];
  private isShow:boolean=true;
  ngOnInit(){
    let href = window.location.href;
    //判断刷新时的链接，从而指定当前三级菜单是否被选中
    this.menu.map(item => {
      item.choosed = false;
      if(href.indexOf(item.link)>1){
        item.choosed = true;
      }
    })
  }
  hideMenu(isShow){
    this.isShow = isShow;
  }

}



